@import '../../global.scss';

.topbar {
	width: 100%;
	height: 70px;
	background-color: #fff;
	color: $mainColor;
	position: fixed;
	top: 0;
	z-index: 3;
	transition: all 0.8s ease;

	.wrapper {
		padding: 10px 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;

		.left {
			display: flex;
			align-items: center;

			.logo {
				font-size: 40px;
				font-weight: 700;
				text-decoration: none;
				color: inherit;
				margin-right: 40px;
			}

			.itemContainer {
				margin-left: 30px;
				
				@include mobile {
					display: none;
				}

				.itemBox {
					display: flex;
					align-items: center;
					text-decoration: none;
					color: $mainColor;

					.icon {
						font-size: 18px;
						margin-right: 5px;
					}

					span {
						font-size: 15px;
						font-weight: 500;
					}
				}

				.email {					
					cursor: text;					
				}
			}
		}

		.right {
			.hamburger {
				width: 32px;
				height: 25px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				cursor: pointer;

				span {
					width: 100%;
					height: 3px;
					background-color: $mainColor;
					transform-origin: left;
					transition: all 0.8s ease-out;
				}
			}
		}
	}

	&.active {
		background-color: $mainColor;
		color: #fff;

		.wrapper {
			.left {
				.itemContainer {
					.itemBox {
						color: #fff;
					}
				}
			}
		}

		.hamburger {
			span {
				&:first-child {
					background-color: #fff;
					transform: rotate(45deg);
				}
				&:nth-child(2) {
					opacity: 0;
				}
				&:last-child {
					background-color: #fff;
					transform: rotate(-45deg);
				}
			}
		}
	}
}
